<template>
    <div>
         <div class="col-sm-12">
            <div class="col-sm-2">
                <h5 ><span v-text="statetitle"></span>{{opreatetitle}}</h5>
            </div>
            <div class="col-sm-12">
                <form class="form form-horizontal col-sm-8" name="myform">
                  <!-- 特有输入栏 菜单类别 -->
                   <div v-if="stateId=='gongnengguanli'"  class="form-group">
                      <!-- 选择菜单级别 -->
                      <label for="" class="col-sm-2 control-label">类别</label>
                        <div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type1" value="1" v-model="addData.type"><label for="type1" class="control-label">目录</label> 
                          </div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type2" value="2" :checked="true" v-model="addData.type"><label for="type2"  class="control-label">菜单</label> 
                          </div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type3" value="3" v-model="addData.type"><label for="type3"  class="control-label">按钮</label> 
                          </div>
                        </div>
                   </div>
                   <!-- 公共输入栏 -->
                    <div class="form-group" >
                        <label class="control-label col-sm-2"><span v-text="statetitle"></span>名</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.name!='','has-error':errors.has('name')}">
                            <input type="text" class="form-control" name="name" :placeholder="'请输入'+statetitle+'名,最多为20位'" v-model="addData.name" :data-vv-as="statetitle+'名'"
                             v-validate="validateAdd('name')">
                            </div>
                             <div class="text-danger">
                                 <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                        </div>
                    </div>
                    <!-- 特有输入栏 -->
                    <div v-if="stateId=='gongnengguanli'">
                      <!--菜单url-->
                      <div class="form-group" v-if="addData.type==2">
                        <label class="control-label col-sm-2">菜单url</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('url')&&addData.url!='','has-error':errors.has('url')}">
                            <input type="text" name="url" class="form-control" placeholder="请输入菜单url,只能输入英文和数字" v-model="addData.url" 
                              data-vv-as="url"   v-validate="validateAdd('url')"></div>
                             <div  class="text-danger">
                            <span v-show="errors.has('url')">{{errors.first("url")}}</span>
                        </div>
                    </div>
                      <!-- 上级菜单 -->
                    <div class="form-group">
                        <label for="" class="col-sm-2 control-label">上级菜单</label>
                        <div class="col-sm-6">
                          <input type="text" class="form-control"  v-model="addData.parentName" @click="menuTree" readonly="readonly">
                        </div>
                    </div>
                      <!-- 授权标识 -->
                    <div class="form-group" v-if="addData.type!=1">
                        <label for="" class="col-sm-2 control-label">授权标识</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('perms')&&addData.perms!='','has-error':errors.has('perms')}">
                          <input type="text" class="form-control" placeholder="用多个逗号分隔；如user:list,user:delete" v-model="addData.perms" name="perms" data-vv-as="授权标识" v-validate="'required'">
                        </div>
                         <div class="text-danger">
                          <span v-show="errors.has('perms')">{{errors.first("perms")}}</span>
                        </div>
                    </div>
                      <!-- 排序号 -->
                    <div v-if="addData.type!=3">
                        <div class="form-group">
                        <label for="" class="col-sm-2 control-label">排序号</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('order_num')&&addData.order_num!='','has-error':errors.has('order_num')}">
                          <input type="number" class="form-control" v-model="addData.order_num" data-vv-as="排序号" v-validate="'required'" name="order_num">
                        </div>
                         <div class="text-danger">
                          <span v-show="errors.has('order_num')">{{errors.first("order_num")}}</span>
                        </div>
                    </div>
                      <!-- 图标 -->
                    <div class="form-group">
                        <label for="" class="col-sm-2 control-label">图标</label>
                        <div class="col-sm-6">
                          <input type="text" class="form-control" placeholder="菜单图标" name="icon" v-model="addData.icon">
                        </div>
                    </div>
                      </div>
                    </div>
                     <!-- 公共输入栏 描述 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">详情描述</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('mydetail')&&addData.detail!='','has-error':errors.has('mydetail')}">
                            <textarea rows="6" class="form-control" name="mydetail" placeholder="请输入描述信息，最多不超过50个字符" v-model="addData.detail" data-vv-as="描述"
                            v-validate="'required|cnlength:50'"></textarea>
                            </div>
                         <div class="text-danger">
                          <span v-show="errors.has('mydetail')">{{errors.first("mydetail")}}</span>
                        </div>
                    </div>
                    <!-- 按钮 -->
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button> 
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddclick">返回</button> 
                    </div>
                </form>
            </div>
            <div id="menuLayer" style="display: none;padding:10px;">
                <ul id="menuTree" class="ztree"></ul>
            </div>
        </div>
    </div>
</template>
<script>
import ztree from "ztree";
import "ztree/css/metroStyle/metroStyle.css";
import "ztree/js/jquery.ztree.all.min.js";
import "@/assets/layer/layer.js";
import "@/assets/layer/skin/default/layer.css";
export default {
  data: () => {
    return {
      initData: {
        id: "",
        name: "",
        url: "",
        detail: "",
        type: "2",
        order_num: 0,
        perms: "",
        parentName: "一级菜单",
        parentId: 0
      },
      addData: {},
      nameVer: "",
      urlVer: "",
      ztree: {},
      opreatetitle: "添加"
    };
  },

  props: {
    stateId: String,
    statetitle: String,
    checkData: Array,
    opreate: String
  },
  watch: {
    checkData(newvalue) {
      this.logincheckData();
    }
  },
  mounted: function() {
    this.logincheckData();
    const validate = (value, args) =>
      new Promise(resolve => {
        if (value) {
          var data = {
            data: {
              data: value,
              name: args[0]
            },
            states: {
              id: this.stateId
            }
          };
          return resolve(
            this.$axios
              .post(this.HOST + "inspectData.action", data)
              .then(function(success) {
                console.log(success.data);
                if (success.data.code == "0") {
                  return {
                    valid: true
                  };
                } else if ((success.data.code = "500")) {
                  return {
                    valid: false,
                    data: {
                      message: success.data.msg
                    }
                  };
                }
              })
              .catch(function(error) {
                console.log(error);
                console.log("网络错误");
                return {
                  valid: false,
                  data: {
                    message: ""
                  }
                };
              })
          );
        } else {
          return false;
        }
      });
    const val = {
      getMessage: function(name) {
        return "已存在";
      },
      validate: validate
    };
    this.$validator.extend("val", val);
    this.getMenu();
  },
  filters: {},
  watch: {
    opreate(news) {
      this.logincheckData();
    }
  },
  methods: {
    logincheckData() {
      var data;
      if (this.opreate == "update") {
        if (this.checkData.length > 0) {
          data = this.$api.opreate.ObjectCopy(this.checkData);
          this.addData = this.cutStr("detail", "name", data[0]);
          this.opreatetitle = "修改";
        }
      } else if (this.opreate == "add") {
        this.addData = this.$api.opreate.ObjectCopy(this.initData);
        this.opreatetitle = "添加";
      }
    },
    cutStr(name1, name2, Obj) {
      var obj = Obj;
      for (var key in obj) {
        if (key.indexOf(name1) > -1 && key != name1) {
          obj[name1] = obj[key];
          delete obj[key];
        }
        if (key.indexOf(name2) > -1 && key != name2) {
          obj[name2] = obj[key];
          delete obj[key];
        }
      }
      return obj;
    },
    validateAdd(name) {
      var Str = "required|cnlength:20";
      var Addstr = Str + "|val:" + name;
      if (this.opreatetitle == "添加") {
        return Addstr;
      } else {
        return Str + "";
      }
    },
    inforAddclick() {
      this.addData = this.$api.opreate.ObjectCopy(this.initData);
      this.$validator.errors.clear()
      this.$api.opreate.emitCall("inforCall", false, this);
    },
    //数据添加操作
    addDatas: function() {
      var vm = this;
      var stateId = vm.stateId;
      var data = vm.addData;
      var datas = {
        data: data,
        states: {
          id: stateId
        }
      };
      console.log(datas);
      // 修改操作
      if (vm.opreatetitle == "修改") {
         vm.$api.http.opreateHttp(vm,"updatePageInf.action", datas,"修改");
      } else if (vm.opreatetitle == "添加") {
        //添加操作
        vm.$api.http.opreateHttp(vm,"distributor.action", datas,"添加");
      }
    },
    menuTree: function() {
      var vm = this;
      layer.open({
        type: 1,
        offset: "50px",
        skin: "layui-layer-molv",
        title: "选择菜单",
        area: ["300px", "450px"],
        shade: 0,
        shadeClose: false,
        content: jQuery("#menuLayer"),
        btn: ["确定", "取消"],
        btn1: function(index) {
          var node = vm.ztree.getSelectedNodes();
          //选择上级菜单
          vm.addData.parentName = node[0].name;
          vm.addData.parent_id = node[0].menuId;
          layer.close(index);
          console.log(vm.addData);
        }
      });
    },
    getMenu: function(menuId) {
      var setting = {
        data: {
          simpleData: {
            enable: true,
            idKey: "menuId",
            pIdKey: "parentId",
            rootPId: -1
          },
          key: {
            url: "nourl"
          }
        }
      };

      //加载菜单树
      //设置ztree的数据
      var name = {
        menuList: [
          {
            menuId: 35,
            parentId: 0,
            name: null,
            url: null,
            perms: null,
            type: 1,
            icon: null,
            orderNum: 0,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 36,
            parentId: 0,
            name: null,
            url: null,
            perms: null,
            type: 1,
            icon: null,
            orderNum: 0,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 37,
            parentId: 30,
            name: null,
            url: null,
            perms: null,
            type: 1,
            icon: null,
            orderNum: 2,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 34,
            parentId: 0,
            name: "123",
            url: null,
            perms: null,
            type: 0,
            icon: null,
            orderNum: 34,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 30,
            parentId: 0,
            name: "定时任务",
            url: "",
            perms: "",
            type: 0,
            icon: "fa fa-clock-o",
            orderNum: 100,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 6,
            parentId: 30,
            name: "任务管理",
            url: "schedule/job.html",
            perms: "",
            type: 1,
            icon: "",
            orderNum: 110,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 14,
            parentId: 30,
            name: "任务日志",
            url: "schedule/joblog.html",
            perms: "schedule:joblog:list",
            type: 1,
            icon: "",
            orderNum: 120,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 31,
            parentId: 0,
            name: "系统监控",
            url: "",
            perms: "",
            type: 0,
            icon: "fa fa-binoculars",
            orderNum: 200,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 5,
            parentId: 31,
            name: "SQL监控",
            url: "druid/index.html",
            perms: "",
            type: 1,
            icon: "fa fa-bug",
            orderNum: 210,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 29,
            parentId: 31,
            name: "系统日志",
            url: "sys/log.html",
            perms: "sys:log:list",
            type: 1,
            icon: "fa fa-file-text-o",
            orderNum: 220,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 1,
            parentId: 0,
            name: "系统管理",
            url: "",
            perms: "",
            type: 0,
            icon: "fa fa-cog",
            orderNum: 300,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 2,
            parentId: 1,
            name: "用户管理",
            url: "sys/user.html",
            perms: "",
            type: 1,
            icon: "fa fa-user",
            orderNum: 310,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 3,
            parentId: 1,
            name: "角色管理",
            url: "sys/role.html",
            perms: "",
            type: 1,
            icon: "fa fa-user-secret",
            orderNum: 320,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 4,
            parentId: 1,
            name: "菜单管理",
            url: "sys/menu.html",
            perms: "",
            type: 1,
            icon: "fa fa-th-list",
            orderNum: 330,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 27,
            parentId: 1,
            name: "参数管理",
            url: "sys/config.html",
            perms:
              "sys:config:list,sys:config:info,sys:config:save,sys:config:update,sys:config:delete",
            type: 1,
            icon: "fa fa-sun-o",
            orderNum: 340,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 28,
            parentId: 1,
            name: "代码生成器",
            url: "sys/generator.html",
            perms: "sys:generator:list,sys:generator:code",
            type: 1,
            icon: "fa fa-rocket",
            orderNum: 349,
            parentName: null,
            open: null,
            list: null
          },
          {
            menuId: 0,
            parentId: -1,
            name: "一级菜单",
            url: null,
            perms: null,
            type: null,
            icon: null,
            orderNum: null,
            parentName: null,
            open: true,
            list: null
          }
        ],
        code: 0
      };
      var vm = this;
      vm.ztree = $.fn.zTree.init($("#menuTree"), setting, name.menuList);
      //编辑（update）时，打开tree，自动高亮选择的条目
      var node = vm.ztree.getNodeByParam("menuId", vm.addData.parentId);
      //选中tree菜单中的对应节点
      vm.ztree.selectNode(node);
      //编辑（update）时，根据当前的选中节点，为编辑表单的“上级菜单”回填值
      vm.addData.parentName = node.name;
    }
  }
};
</script>





